/************************************************

Stylesheet: Blocks Backend Stylesheet

*************************************************/
.block-editor{
    .bar-cta{
        background: red;
        height: 108px;
    }
}
$content__width: 768px;
$content__width--wide: 1024px;
.wp-block {
	max-width: $content__width;
  
	&[data-align="wide"] {
		max-width: $content__width--wide;
	}

	&[data-align="full"] {
		max-width: none;
	}
}